<!-- 修改 by czh 添加要做的问卷页面 -->
<template>
    <div class="quest" style="min-height:85vh;">
        <div id="top"/>
        <div class="dire-quest" v-if="qActive === true && speedCheck === true">
            <div class="block">
                <h1>The Simulator Sickness Questionnaire （SSQ）</h1>
                <div style="width: 50vh;margin: auto;display: flex;" v-if="isFirest">
                    <span>氏名</span>
                    <el-input v-model="input" placeholder="请输入氏名"></el-input>
                </div>
                <div v-for="item in questList" :key="item.id" class="demonstration">
                    <span class="demonstration">
                        Q{{ item.id }} 質問{{ item.id }}<br>
                        {{ item.englishQuest }} <br>
                        {{ item.japaneseQuest }} <p></p>
                    </span>
                    <el-radio-group v-model="item.score">
                        <el-radio :label="0">まったくない</el-radio>
                        <el-radio :label="1">少しある</el-radio>
                        <el-radio :label="2">中程度にある</el-radio>
                        <el-radio :label="3">大いにある</el-radio>
                    </el-radio-group>
                </div>
            </div>
            <el-button type="primary" @click="calc">回答</el-button>
        </div>
        <div class="webSpeed" v-if="speedCheck === false">
            <h2>ネットの速度が遅いです！ほかの環境でお試しください！！</h2>
        </div>
        <div class="result" v-if="qActive === false">
            <el-button type="primary" @click="goToTest" v-if="isFirest">続き</el-button>
            <el-button type="primary" @click="finsihTest" v-else>完成</el-button>
        </div>
    </div>
</template>

<script>
    
    export default {
        data() {
            return {
                input:'', // 姓名
                isFirest:true,
                qActive: true,
                speedCheck: true,
                re: null,
                colors: { 2: '#F7BA2A', 4: { value: '#FF9900', excluded: true }, 5: '#ff6347', 7: '#dc143c' },
                iconClasses: ['el-icon-help', 'el-icon-help', 'el-icon-help'],
                questList: [
                    {id:1,englishQuest:'General diacomfort',japaneseQuest:'全般的に気分が悪い',score:null},
                    {id:2,englishQuest:'Fatigue',japaneseQuest:'疲労感がある',score:null},
                    {id:3,englishQuest:'Headache',japaneseQuest:'頭痛がする',score:null},
                    {id:4,englishQuest:'Eyestrain',japaneseQuest:'目の疲れを感じる',score:null},
                    {id:5,englishQuest:'Difficulty focusing',japaneseQuest:'目の焦点が合わせにくい',score:null},
                    {id:6,englishQuest:'Increased salivation',japaneseQuest:'唾液がよく出る',score:null},
                    {id:7,englishQuest:'Sweating',japaneseQuest:'冷や汗が出る',score:null},
                    {id:8,englishQuest:'Nausea',japaneseQuest:'吐き気がする',score:null},
                    {id:9,englishQuest:'Difficulty concentrating',japaneseQuest:'注意集中が困難である',score:null},
                    {id:10,englishQuest:'Fullness of head',japaneseQuest:'頭重感がする',score:null},
                    {id:11,englishQuest:'Blurred vision',japaneseQuest:'視界がぼやける',score:null},
                    {id:12,englishQuest:'Dizzy＜eye open＞',japaneseQuest:'開眼で身体がふらつく',score:null},
                    {id:13,englishQuest:'Dizzy＜eye closed＞',japaneseQuest:'閉眼で身体がふらつく',score:null},
                    {id:14,englishQuest:'Vertigo',japaneseQuest:'回転性のめまいがする',score:null},
                    {id:15,englishQuest:'Stomach awareness',japaneseQuest:'胃重感がする',score:null},
                    {id:16,englishQuest:'Burping',japaneseQuest:'ゲップが出る',score:null},
                ],
                scoreTotal:{
                    NScore:0,
                    OScore:0,
                    DSocre:0,
                    TSScore:0,
                }
            }
        },
        methods: {
            calc(){
                let calcN = [0,5,6,7,8,14,15]
                let calcO = [0,1,2,3,4,8,10]
                let calcD = [4,7,9,10,11,12,13]
                let NScore=0,OScore=0,DSocre=0,TSScore=0
                let questScoreList = this.questList.map(item=>item.score)
                calcN.forEach(item => NScore += questScoreList[item] || 0)
                calcO.forEach(item => OScore += questScoreList[item] || 0)
                calcD.forEach(item => DSocre += questScoreList[item] || 0)
                questScoreList.forEach(item => TSScore += item || 0)
                NScore = parseFloat(NScore * 9.54).toFixed(2)
                OScore = parseFloat(OScore * 7.58).toFixed(2)
                DSocre = parseFloat(DSocre * 13.92).toFixed(2)
                TSScore = parseFloat(TSScore * 3.74).toFixed(2)
                console.log(NScore,'NScore')
                console.log(OScore,'OScore')
                console.log(DSocre,'DSocre')
                console.log(TSScore,'TSScore')
                this.scoreTotal.NScore = NScore
                this.scoreTotal.OScore = OScore
                this.scoreTotal.DSocre = DSocre
                this.scoreTotal.TSScore = TSScore
                this.qActive =false
            },
            goToTest(){
                // 该方法用来做跳转 并对第一次的分数进行记录
                let data = this.getData() || []
                let newDate = new Date()
                let questScoreList = {}
                this.questList.forEach((item,index)=>{
                    questScoreList[`Q${index+1}`] = item.score
                })

                data.push({first:questScoreList,uniqueId:this.$route.query.uniqueId,firstCalc:this.scoreTotal,name:this.input,time:newDate.toUTCString()})
                this.setLocalStorage(data)
                this.$router.push({path:'/About',query:{title:this.$route.query.title,uniqueId:this.$route.query.uniqueId}})
            },
            getData(){
                // 获取浏览器内 localStorage 的用户数据
                let data = window.localStorage.getItem('userAccountScore')
                data = JSON.parse(decodeURIComponent(data))
                return data
            },
            setLocalStorage(data){
                window.localStorage.setItem('userAccountScore',encodeURIComponent(JSON.stringify(data)))
            },
            init(){
                let data = this.getData() || []
                console.log(data)
                let uniqueIdList = data.map(item => item.uniqueId)
                this.isFirest = !uniqueIdList.includes(this.$route.query.uniqueId)
            },
            finsihTest(){
                let data = this.getData()
                let questScoreList = {}
                this.questList.forEach((item,index)=>{
                    questScoreList[`Q${index+1}`] = item.score
                })
                let setData = data.map(item => {
                    if(item.uniqueId === this.$route.query.uniqueId){
                        item = {...item,second:questScoreList,secondCalc:this.scoreTotal}
                    }
                    return item
                })
                this.setLocalStorage(setData)
                this.$router.push('/')
            }
        },
        mounted(){
            this.init()
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

.block{
    margin-top: 10px;
}
.demonstration{
    margin-top: 15px;
}

.el-button{
    margin-top: 20px;
}

.sub_menu{
  margin: 0em;
}
</style>
